body {
  margin: 0;
  padding: 0;
  background: #0f0f23;
  font-family: "Source Code Pro", monospace;
  font-weight: 300;
  font-size: 14pt;
  color: #e9e9e9;
  min-width: 600px;
}

.navbar {
  display: flex;
  justify-content: center;
  height: 80px;
  background-color: #0f0f23;
}

.navbar a {
  color: #656565;
  text-decoration: none;
  padding: 30px 30px;
  text-align: center;
}

.navbar a:hover {
  color: #c79c8c;
}

.navbar a.active {
  color: #ead6cf;
}

.main-content {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px 40px;
  height: calc(100vh - 140px);
}

.main-content a:link {
  color: #ead6cf;
  text-decoration: none;
}

.main-content a:visited {
  color: #ead6cf;
  text-decoration: none;
}

.main-content a:hover {
  color: #c79c8c;
  text-decoration: none;
}

.intro-content {
  width: 563px;
  padding: 10px;
  text-align: justify;
}

.intro-content h1 {
  font-size: 24pt;
  margin-block-start: 22px;
  margin-block-end: 22px;
}

.intro-content p {
  line-height: 1.6;
  margin-block-start: 19px;
  margin-block-end: 21px;
}

.intro-content img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 45%;
  border: 9px solid #656565;
  border-radius: 10px;
}

.research-content {
  width: 950px;
  padding: 10px;
  text-align: justify;
}

.research-content h2 {
  font-size: 20pt;
  margin-block-start: 38px;
  margin-block-end: 6px;
}

.research-content p {
  line-height: 1.6;
  margin-block-start: 6px;
  margin-block-end: 21px;
}

.software-content {
  width: 820px;
  padding: 10px;
  text-align: justify;
}

.software-content h2 {
  font-size: 20pt;
  margin-block-start: 38px;
  margin-block-end: 6px;
}

.software-content p {
  line-height: 1.6;
  margin-block-start: 6px;
  margin-block-end: 21px;
}

.software-link {
  padding: 0px 10px;
}

@media screen and (max-width: 600px) {

  .main-content {
    height: 800px;
  }

  .img-content {
      width: 0px;
  }
}
